import React, { useState, useEffect, Fragment } from "react";
// import MaterialTable from "material-table";
import { useSelector, useDispatch } from "react-redux";
import CircularLoading from "../components/CircularLoading";
import { updateReservacionAction } from "../store/reservaciones/reservacionAction";

export default function HistoryReservas() {
  const [data, setData] = useState([]);
  const reservaciones = useSelector((state) => state.reservaciones);
  const dispatch = useDispatch();

  useEffect(() => {
    if (reservaciones.reservaciones) {
      setData(reservaciones.reservaciones);
    }
  }, [reservaciones.reservaciones]);

  return reservaciones.fetching ? (
    <CircularLoading />
  ) : (
    <Fragment>
      <div className="row m-0 p-0">
        {/* =======alerta
        {form.alert ? (
          <div
            className={`alert ${form.alert} text-center my-4 col-12`}
            role="alert"
          >
            {form.msjAlert}
          </div>
        ) : (
          ""
        )} */}
        <div className="col-12">
          <div className="card">
            <div className="card-body">
              <div className="d-flex justify-content-between">
                <h4 className="header-title mt-0 mb-1">
                  Historial de Servicios
                </h4>
              </div>

              <div className="table-responsive">
                <table
                  id="datatable-buttons"
                  className="table table-striped dt-responsive nowrap"
                >
                  <thead>
                    <tr>
                      <th>Cliente</th>
                      <th>Vehiculo</th>
                      <th>Taller</th>
                      <th>Servicio</th>
                      <th>Costo</th>
                      <th>Agendar cita</th>
                      <th>Estado</th>
                    </tr>
                  </thead>

                  <tbody>
                    {data.map((item) => {
                      const colorText =
                        item.estado === "rechazado"
                          ? "text-danger"
                          : item.estado === "cancelado"
                          ? "text-warning"
                          : item.estado === "aprovado"
                          ? "text-info"
                          : item.estado === "exitoso"
                          ? "text-success"
                          : "";
                      return (
                        <tr key={item._id}>
                          <td>{item.usuario.nombre}</td>
                          <td>{item.vehiculo.modelo}</td>
                          <td>{item.taller.nombre}</td>
                          <td>{item.servicio.servicio}</td>
                          <td>{item.costo}</td>
                          <td>{`${item.dia} ${item.hora}`}</td>
                          <td className={colorText}>{item.estado}</td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            {/* <!-- end card body--> */}
          </div>
          {/* <!-- end card --> */}
        </div>
        {/* <!-- end col--> */}
      </div>
    </Fragment>
  );
}
