import axios from "axios";
import config from "../../config";
import {
  RESERVACION,
  RESERVACION_SUCCESS,
  RESERVACION_ERROR,
} from "./reservacionReducer";

export const fetchReservacionesAction = () => async (dispatch, getState) => {
  dispatch({
    type: RESERVACION,
  });

  try {
    const response = await axios.get(`${config.API_URL}/reservaciones`);
    dispatch({
      type: RESERVACION_SUCCESS,
      payload: {
        reservaciones: response.data.data,
      },
    });

    // return response;
  } catch (error) {
    dispatch({
      type: RESERVACION_ERROR,
      payload: { message: "Ha ocurrido un error" },
    });

    // return error;
  }
};

export const getReservacionAction = (id) => async (dispatch, getState) => {
  dispatch({
    type: RESERVACION,
  });

  try {
    const response = await axios.get(`${config.API_URL}/reservacion/${id}`);
    dispatch({
      type: RESERVACION_SUCCESS,
      payload: {
        reservacion: response.data,
        message: response.message,
      },
    });

    // return response;
  } catch (error) {
    dispatch({
      type: RESERVACION_ERROR,
      payload: { message: "Ha ocurrido un error" },
    });

    // return error;
  }
};

export const createReservacionAction = (form) => async (dispatch, getState) => {
  dispatch({
    type: RESERVACION,
  });

  try {
    const response = await axios.post(`${config.API_URL}/reservacion`, form);

    dispatch({
      type: RESERVACION_SUCCESS,
      payload: {
        message: response.message,
      },
    });
    // return response;
  } catch (error) {
    dispatch({
      type: RESERVACION_ERROR,
      payload: { message: "Ha ocurrido un error" },
    });

    // return error;
  }
};
