import React, { useState, useEffect, Fragment } from "react";
// import MaterialTable from "material-table";
import { useSelector, useDispatch } from "react-redux";
import CircularLoading from "../components/CircularLoading";
import {
  updateReservacionAction,
  fetchReservacionesAction,
} from "../store/reservaciones/reservacionAction";

export default function Notifications() {
  const [data, setData] = useState([]);
  const reservaciones = useSelector((state) => state.reservaciones);
  const dispatch = useDispatch();

  useEffect(() => {
    if (reservaciones.reservaciones) {
      const pendientesData = reservaciones.reservaciones.filter(
        (item) => item.estado === "pendiente"
      );
      setData(pendientesData);
    }
  }, [reservaciones.reservaciones]);

  const rechazarSolicitud = (id, item) => {
    dispatch(updateReservacionAction(id, { estado: "rechazado" }));
    dispatch(fetchReservacionesAction());
  };
  const aprovarSolicitud = (id, item) => {
    dispatch(updateReservacionAction(id, { estado: "aprovado" }));
    dispatch(fetchReservacionesAction());
  };

  return reservaciones.fetching ? (
    <CircularLoading />
  ) : (
    <Fragment>
      <div className="row m-0 p-0">
        {/* =======alerta
        {form.alert ? (
          <div
            className={`alert ${form.alert} text-center my-4 col-12`}
            role="alert"
          >
            {form.msjAlert}
          </div>
        ) : (
          ""
        )} */}
        <div className="col-12">
          <div className="card">
            <div className="card-body">
              <div className="d-flex justify-content-between">
                <h4 className="header-title mt-0 mb-1">
                  Solicitudes de Servicios
                </h4>
              </div>

              <div className="table-responsive">
                <table
                  id="datatable-buttons"
                  className="table table-striped dt-responsive nowrap"
                >
                  <thead>
                    <tr>
                      <th>Cliente</th>
                      <th>Vehiculo</th>
                      <th>Taller</th>
                      <th>Servicio</th>
                      <th>Costo</th>
                      <th>Agendar cita</th>
                      <th>Estado</th>
                      <th>Acciones</th>
                    </tr>
                  </thead>

                  <tbody>
                    {data.map((item) => {
                      return (
                        <tr key={item._id}>
                          <td>{item.usuario.nombre}</td>
                          <td>{item.vehiculo.modelo}</td>
                          <td>{item.taller.nombre}</td>
                          <td>{item.servicio.servicio}</td>
                          <td>{item.costo}</td>
                          <td>{`${item.dia} ${item.hora}`}</td>
                          <td>{item.estado}</td>

                          <td className="d-flex flex-column">
                            <button
                              type="buttom"
                              className="btn btn-danger mb-2 btn-sm"
                              onClick={() => rechazarSolicitud(item._id, item)}
                            >
                              Rechazar
                            </button>
                            <button
                              type="buttom"
                              className="btn btn-primary btn-sm"
                              onClick={() => {
                                aprovarSolicitud(item._id, item);
                              }}
                            >
                              Aprovar
                            </button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            {/* <!-- end card body--> */}
          </div>
          {/* <!-- end card --> */}
        </div>
        {/* <!-- end col--> */}
      </div>
    </Fragment>
  );
}
