// constanst
let initialData = {
  fetching: false,
  error: "",
  message: "",
  reservaciones: [],
  reservacion: {},
  search: [],
};

export const RESERVACION = "RESERVACION";
export const RESERVACION_SUCCESS = "RESERVACION_SUCCESS";
export const RESERVACION_ERROR = "RESERVACION_ERROR";

// reducer
export default function reducer(state = initialData, action) {
  switch (action.type) {
    //Login
    case RESERVACION:
      return { ...state, fetching: true };
    case RESERVACION_SUCCESS:
      return { ...state, fetching: false, ...action.payload };
    case RESERVACION_ERROR:
      return { ...state, fetching: false, error: action.payload };

    default:
      return state;
  }
}
