import axios from "axios";
import config from "../../config";
import {
  CATEGORIA,
  CATEGORIA_SUCCESS,
  CATEGORIA_ERROR,
} from "./categoriaReducer";

export const fetchCategoriasAction = () => async (dispatch, getState) => {
  dispatch({
    type: CATEGORIA,
  });

  try {
    const response = await axios.get(`${config.API_URL}/categorias`);
    dispatch({
      type: CATEGORIA_SUCCESS,
      payload: {
        categorias: response.data.data,
      },
    });

    // return response;
  } catch (error) {
    dispatch({
      type: CATEGORIA_ERROR,
      payload: { message: "Ha ocurrido un error" },
    });

    // return error;
  }
};

export const createCategoriaAction = (form) => async (dispatch, getState) => {
  dispatch({
    type: CATEGORIA,
  });

  try {
    const response = await axios.post(`${config.API_URL}/categoria`, form);
    fetchCategoriasAction();
    dispatch({
      type: CATEGORIA_SUCCESS,
      payload: {
        message: response.message,
      },
    });
    // return response;
  } catch (error) {
    dispatch({
      type: CATEGORIA_ERROR,
      payload: { message: "Ha ocurrido un error" },
    });

    // return error;
  }
};
